<script>
import { defineComponent } from 'vue';
import { useDisplay } from 'vuetify'
import { computed } from 'vue'

// Components

export default defineComponent({
  name: 'AboutView',
  data () {
    return {
      // images: [ 'itba_black.png', 'JPM.png', 'udesa_transparent.png' ]
      images: [ 'itba_black.png' ,'IB.png']

    }
  },
  setup () {
    const { name } = useDisplay()

    const width = computed(() => {
      // name is reactive and
      // must use .value
      switch (name.value) {
        case 'xs': return 200
        case 'sm': return 250
        case 'md': return 250
        case 'lg': return 250
        case 'xl': return 250
        case 'xxl': return 250
      }

      return undefined
    })

    const bottom_width = computed(() => {
      // name is reactive and
      // must use .value
      switch (name.value) {
        case 'xs': return 100
        case 'sm': return 150
        case 'md': return 150
        case 'lg': return 150
        case 'xl': return 150
        case 'xxl': return 150
      }

      return undefined
    })

    const bottom_height = computed(() => {
      // name is reactive and
      // must use .value
      switch (name.value) {
        case 'xs': return 50
        case 'sm': return 100
        case 'md': return 100
        case 'lg': return 150
        case 'xl': return 150
        case 'xxl': return 150
      }

      return undefined
    })

    return {width, bottom_width, bottom_height}
  }
});
</script>


